            "introText": "Somos un servidor privado de L2 orientado a la jugabilidad M.O.B.A. nicos en el gnero. Desarrollado por/para fanticos exigentes. Venimos a ofrecer un juego mucho ms dinmico y competitivo que nunca, enfocados por completo en los combates grupales (Party vS Party, CvC & AvA). Consideramos haber eliminado de raz las injusticias natas de nuestro amado juego, construyendo un sistema equitativo, transparente, y duradero. En Newline conoceremos finalmente a los mejores jugadores & clanes del mundo.",
