                "url": "news/nlteam",
                "title": "Newline Team",
                "intro": "United by passion for an extraordinary game, and eager to make history. We invite you to meet the members of this great work team. We tell you about our vision and mission.",
                "txt": " Somos Newline, y en ese nombre llevamos toda nuestra esencia. Empata, pasin, compromiso, experiencia, y entre otras tantas, una de las que sentmos ms importantes: <u>Positividad</u>, ante todo!<br>Esta postura auto-impuesta en todos nosotros es la que nos permite seguir superandonos, tanto individual, como colectivamente. Somos conscientes, sin estas aptitudes, no estaramos hoy ac.<br><br><br><img src='/img/news/test.jpg' alt='Martin Galenda' title='Project Manager'> <br><br><h2>Misin</h2><br> <ul><li>- Brindarle diversin a todos nuestros usuarios, pudiendo albergar a la comunidad entera de L2.</li><li>- Ofrecer diversas jugabilidades. Para el usuario casual, o competitivo</li><li>- Brindarle diversin a todos nuestros usuarios, albergando a la comunidad entera en distintos servidores.</li><li>- Brindarle diversin a todos nuestros usuarios, albergando a la comunidad entera en distintos servidores.</li><li>- Brindarle diversin a todos nuestros usuarios, albergando a la comunidad entera en distintos servidores.</li></ul> - <br><br><br><h2>Visin</h2> - <br><br><br><h2>El equipo</h2><br>Un grupo de fanaticos unidos por un sueo. Presentemos al equipo, y vayanse preparando, que en cualquier momento empiezo a entrevistarlos!<br><br><h3>Direccin</h3><br>- Redline - CEO - ARG<br>- Batsheva - COE - ES <br>" ,
